import { NavLink as Link } from "react-router-dom";
import styled, { css} from "styled-components";
import "../App.css";

export const Navnb = styled.nav`
  ${(props) => {
    switch (props.$mode) {
      case "dark":
        return css`
          background-color: white;
          width: 95%;
          position: fixed;
          z-index: 2;
          height: 64px;
          box-sizing: border-box;
          display: flex;
          justify-content: space-between;
          align-items: center;
          top: 32px;
          left: 2.5%;
        `;
      default:
        return css`
          background-color: #0000;
          width: 95%;
          position: fixed;
          z-index: 2;
          height: 64px;
          box-sizing: border-box;
          display: flex;
          justify-content: space-between;
          align-items: center;
          top: 32px;
          left: 2.5%;
          transition: 0.2s;
        `;
    }
  }}
`;

export const NavLink = styled(Link)`
  ${(props) => {
    switch (props.$mode) {
      case "dark":
        return `
          color: black;
            font-weight: 900;
  font-size: 18px;
  letter-spacing: 2px;
  align-items: center;
  text-decoration: none;
  padding: 0 40px 0 40px;
  height: 100%;
  cursor: pointer;
  &.active {
    color: var(--accent);
    text-decoration: underline;
    text-decoration: color;
    text-decoration-color: var(--accent);
    text-decoration-thickness: 6px;
    text-underline-offset: 10px;
  }
  &:hover{
    color: var(--accent);
    text-decoration: underline;
    text-decoration: color;
    text-decoration-color: var(--accent);
    text-decoration-thickness: 6px;
    text-underline-offset: 10px;
  }

        `;
      default:
        return `
          color: white;
            font-weight: 900;
  font-size: 18px;
  letter-spacing: 2px;
  align-items: center;
  text-decoration: none;
  padding: 0 40px 0 40px;
  height: 100%;
  cursor: pointer;
  &.active {
    color: var(--accent);
    text-decoration: underline;
    text-decoration: color;
    text-decoration-color: var(--accent);
    text-decoration-thickness: 6px;
    text-underline-offset: 10px;
  }
  &:hover{
    color: var(--accent);
    text-decoration: underline;
    text-decoration: color;
    text-decoration-color: var(--accent);
    text-decoration-thickness: 6px;
    text-underline-offset: 10px;
  }
        `;
    }
  }}
`;

/*

`; */

export const NavMenu = styled.div`
  display: flex;
  align-items: center;
  display: flex;
  justify-content: right;
  white-space: nowrap;
  @media screen and (max-width: 768px) {
    display: none;
  }
`;

export const NavMenuBurger = styled.div`
  display: flex;
  align-items: center;
  display: flex;
  justify-content: right;
  white-space: nowrap;
  @media screen and (min-width: 768px) {
    display: none;
  }
`;
