function Makuri(props) {
  return (
    <section className="Makuri containerbig">
      <h1 style={{ fontStyle: "oblique", fontWeight: "900" }}>MAKURI-GITAE</h1>
      <img src={require("../images/sword.png")}></img>
      <h5 style={{ letterSpacing: "0.278vw", marginTop: "1.111vw" }}>
        THE BALANCE BETWEEN FLEXIBILITY AND RIGOR
      </h5>
      <div className="mttgrid">
        <div className="sidebar">
          <p style={{ marginTop: "3.333vh" }}>{props.text}</p>
          <h5 style={{ color: "#CE42FF", marginTop: "3.333vh" }}>{props.h5}</h5>
        </div>
      </div>
    </section>
  );
}

export default Makuri;
