import React from 'react';
import { Formik } from "formik";

const Basic = () => (
  <div
    style={{
      height: "100%",
      marginTop: "100px",
      color: "red",
      marginBottom: "150px",
    }}
    className="containerform"
  >
    <h1 style={{ paddingBottom: "32px" }}>HIT US UP!</h1>
    <Formik
      initialValues={{ email: "", project: "" }}
      validate={(values) => {
        const errors = {};
        if (!values.email) {
          errors.email = "*Required";
        } else if (
          !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
        ) {
          errors.email = "Invalid email address";
        }
        return errors;
      }}
      onSubmit={(values, { setSubmitting }) => {
        setTimeout(() => {
          alert(JSON.stringify(values, null, 2));
          setSubmitting(false);
        }, 400);
      }}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        isSubmitting,
        /* and other goodies */
      }) => (
        <div
          style={{
            display: "flex",
            width: "100%",
            justifyContent: "center",
          }}
        >
          <form
            style={{
              display: "flex",
              flexDirection: "column",
              width: "75vw",
            }}
            onSubmit={handleSubmit}
          >
            <div
              style={{
                display: "flex",
                width: "100%",
                justifyContent: "space-between",
                marginBottom: "1.667vw"
              }}
            >
              <input
                type="firstName"
                name="firstName"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.firstName}
                placeholder="First Name"
                style={{
                  fontFamily: "Poppins",
                  padding: "10px",
                  fontWeight: "bold",
                  width: "50%",
                }}
              />
              <input
                type="lastName"
                name="lastName"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.lastName}
                placeholder="Last Name"
                style={{
                  fontFamily: "Poppins",
                  padding: "10px",
                  fontWeight: "bold",
                  width: "50%",
                }}
              />
            </div>
            <input
              type="email"
              name="email"
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.email}
              placeholder="Email"
              style={{
                fontFamily: "Poppins",
                padding: "10px",
                fontWeight: "bold",
              }}
            />
            {errors.email && touched.email && errors.email}
            <textarea
              type="text"
              id="body"
              name="project"
              onChange={handleChange}
              onBlur={handleBlur}
              style={{
                height: "300px",
                resize: "none",
                marginTop: "1.667vw",
                fontFamily: "Poppins",
                padding: "10px",
              }}
              value={values.project}
              placeholder="Tell us a bit about your project..."
            />
            {errors.project && touched.project && errors.project}
            <button
              style={{
                width: "100%",
                marginTop: "3.333vw",
                background: "white",
                fontFamily: "Poppins",
                fontWeight: "bold",
                border: "none",
                padding: "15px",
                letterSpacing: "1.5px",
                borderRadius: "7.5px",
              }}
              type="submit"
              disabled={isSubmitting}
            >
              SUBMIT
            </button>
          </form>
        </div>
      )}
    </Formik>
  </div>
);

export default Basic;
