import { ListGroup } from "reactstrap";
import { useState, useRef, React } from "react";
import Cardo from "./Card";
import { useInView } from "react-intersection-observer";



function Cardslider() {
  const { ref: section, inView: designrefvisible } = useInView({
    rootMargin: "-50% 100% -50%",
  });

  document.documentElement.style.setProperty(
    "--bg",
    ` ${designrefvisible ? "#ffffff" : "black"}`
  );
  document.documentElement.style.setProperty(
    "--text",
    ` ${designrefvisible ? "black" : "white"}`
  );


    const scrollElement = useRef(null);

    const scroll = (scrollOffset) => {
      scrollElement.current.scrollLeft += scrollOffset ;
    };




  return (
    <section ref={section} style={{ height: "100%", paddingLeft: "2.5%" }}>
      <div
        style={{
          width: "100%",
          height: "100%",
          position: "relative",
          paddingBottom: "1.667vw",
          paddingLeft: "2.5%",
        }}
      >
        <div
          style={{
            position: "absolute",
            writingMode: "vertical-rl",
            top: "7%",
            left: "0",
            transform: "rotate(-180deg)",
            fontSize: "1.111vw",
            letterSpacing: "0.278vw",
            color: "var(--text)",
            fontWeight: "bold",
          }}
        >
          LATEST WORK
        </div>
        <h2 style={{ paddingLeft: "4.444vw", width: "60vw" }}>
          WHAT WE'VE BEEN UP TO
        </h2>
        <h6
          style={{
            position: "absolute",
            right: "2.5%",
            bottom: "0",
            color: "var(--accent",
          }}
        >
          SEE MORE &#x279E;
        </h6>
      </div>
      <div
        style={{
          width: "60vw",
          height: "100%",
          position: "relative",
          paddingBottom: "20px",
          display: "flex",
        }}
      ></div>
      <div
        style={{
          position: "relative",
        }}
      >
        <button
          style={{
            position: "absolute",
            top: "50%",
            left: "0%",
            zIndex: "999",
            background: "white",
            textDecoration: "none",
            height: "5vw",
            width: "5vw",
            borderRadius: "5vw",
            fontSize: "1.50vw",
            color: "var(--accent)",
            boxShadow: "0px 0px 25px black",
            border: "none",
            transform: "scaleX(-1)"
          }}
          onClick={() => scroll(-1126)}
        >
          &#x279E;
        </button>
        <button
          style={{
            position: "absolute",
            top: "50%",
            right: "2.5%",
            zIndex: "999",
            background: "white",
            textDecoration: "none",
            height: "5vw",
            width: "5vw",
            borderRadius: "5vw",
            fontSize: "1.50vw",
            color: "var(--accent)",
            boxShadow: "0px 0px 25px black",
            border: "none",
          }}
          onClick={() => scroll(1126)}
        >
          &#x279E;
        </button>
        <div
          style={{
            display: "flex",
            overflowX: "scroll",
            listStyle: "none",
            scrollbarWidth: " none",
            scrollBehavior: "smooth",
          }}
          ref={scrollElement}
        >
          <Cardo
            title="TO INFINITY AND BEYOND"
            text="Brent is a user experience design leader specializing in design strategy, architecture, and interface design. "
            source={require("../images/Toinf.png")}
            bbox="PBS / KCTS9"
          />
          <Cardo
            title="LIFE IS LIKE A BOX OF CHOCOLATES"
            text="Brent is a user experience design leader specializing in design strategy, architecture, and interface design.  "
            source={require("../images/beachchill.png")}
            bbox="AARP / MEMBER BENEFITS"
          />
          <Cardo
            title="WITH GREAT POWER COMES GREAT"
            text="Brent is a user experience design leader specializing in design strategy, architecture, and interface design. "
            source={require("../images/GREATCOMES.png")}
            bbox="RENOVATE AMERICA"
          />
          <Cardo
            title="TO INFINITY AND BEYOND"
            text="Brent is a user experience design leader specializing in design strategy, architecture, and interface design. "
            source={require("../images/beachchill.png")}
            bbox="pbs / kcts9"
          />
          <Cardo
            title="TO INFINITY AND BEYOND"
            text="Brent is a user experience design leader specializing in design strategy, architecture, and interface design. "
            source={require("../images/beachchill.png")}
            bbox="pbs / kcts9"
          />
          <Cardo
            title="TO INFINITY AND BEYOND"
            text="Brent is a user experience design leader specializing in design strategy, architecture, and interface design. "
            source={require("../images/beachchill.png")}
            bbox="pbs / kcts9"
          />
        </div>
      </div>
    </section>
  );
}

export default Cardslider;
