import React, { useEffect, useState } from "react";
import {
  Navnb,
  NavLink,
  NavMenu,
} from "./Topbar2Elements";

import { Nav, Navbar, NavDropdown } from "react-bootstrap";

const Topbar2 = () => {
  const [show, setShow] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);

  const controlTopbar2 = () => {
    if (typeof window !== "undefined") {
      if (window.scrollY > lastScrollY && window.scrollY > 0) {
        // if scroll down hide the Topbar2
        setShow(false);
      } else {
        setShow(true);
      } 

      // remember current page location to use in the next move
      setLastScrollY(window.scrollY);
    }
  };

  useEffect(() => {
    if (typeof window !== "undefined") {
      window.addEventListener("scroll", controlTopbar2);

      // cleanup function
      return () => {
        window.removeEventListener("scroll", controlTopbar2);
      };
    }
  }, [lastScrollY]);

  const [color, setColor] = useState(false)
  const changeColor = () => {
    if (window.scrollY >= 90){
      setColor(true)
    } else {
      setColor(false)
    }
  }

window.addEventListener('scroll',changeColor)

  return (
    <nav className={`display ${show && "hidden"}`}>
      <>
          <Navnb $mode={color ? "dark" : ""}>
          
            <a href="/Mag7site">
              <img
                style={{ width: "64px"}}
                src={
                  color
                    ? require("../images/LOGObg.png")
                    : require("../images/LOGO.png")
                }
                alt="logo"
              ></img>
            </a>
            <NavMenu>
              <NavLink $mode={color ? "dark" : ""} to="/Work" activeStyle>
                WORK
              </NavLink>
              <NavLink $mode={color ? "dark" : ""} to="/Method" activeStyle>
                METHOD
              </NavLink>
              <NavLink $mode={color ? "dark" : ""} to="/Theteam" activeStyle>
                THE TEAM
              </NavLink>
              <NavLink $mode={color ? "dark" : ""} to="/Letschat" activeStyle>
                LET'S CHAT
              </NavLink>
            </NavMenu>
          </Navnb>
      </>
    </nav>
  );
};

export default Topbar2;