import { useInView } from "react-intersection-observer";

function Thebest() {
  const { ref: section, inView: designrefvisible } = useInView({
    rootMargin: "-45% 15% -45%",
  });

  document.documentElement.style.setProperty(
    "--bg",
    ` ${designrefvisible ? "#202228" : "black"}`
  );

  return (
    <section
      ref={section}
      style={{
        position: "relative",
        width: " 100%",
        height: "66.667vw",
        alignItems: "center",
        display: "flex",
      }}
    >
      <div className="containerbig">
        <div
          style={{
            display: "flex",
            gap: "10.208vw",
          }}
        >
          <div
            style={{
              position: "absolute",
              writingMode: "vertical-rl",
              top: "1.111vw",
              transform: "rotate(-180deg)",
              fontSize: "1.111vw",
              letterSpacing: "0.278vw",
              color: "white",
              fontWeight: "bold",
            }}
          >
            LATEST WORK
          </div>
          <img
            style={{ width: "100%", height: "100%"}}
            src={require("../images/teamgames.png")}
          ></img>
          <div>
            <h2>WE'RE THE BEST, AROUND...</h2>
            <h5 style={{ marginTop: "2.222vh" }}>
              NOTHING'S GONNA EVER KEEP US DOWN
            </h5>
            <p style={{ marginTop: "3.889vh" }}>
              Brent is a user experience design leader specializing in design
              strategy, architecture, and interface design. His his UX design
              expertise is bolstered by a command of product management and
              development, making him able to generate big ideas and bring them
              to market.
            </p>
            <h5 style={{ color: "#CE42FF", marginTop: "3.333vh" }}>
              MEET THE TEAM &#x279E;
            </h5>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Thebest;
