import React, { useState } from "react";




function Title(props) {

  var gifs = [
    require("../images/GIFs/kickin.gif"),
    require("../images/GIFs/gif2.gif"),
    require("../images/GIFs/gif3.gif"),
    require("../images/GIFs/gif4.gif"),
    require("../images/GIFs/gif5.gif"),
    require("../images/GIFs/giphy6.gif"),
    require("../images/GIFs/giphy7.gif"),
    require("../images/GIFs/giphy8.gif"),
    require("../images/GIFs/giphy9.gif"),
    require("../images/GIFs/giphy10.gif"),
    require("../images/GIFs/giphy11.gif"),
    require("../images/GIFs/giphy12.gif"),
    require("../images/GIFs/giphy13.gif"),
    require("../images/GIFs/giphy14.gif"),
    require("../images/GIFs/giphy15.gif"),

  ];


  return (
    <section className="Title">
      <div className="page">
        <div className="h4pos">
          <div
            style={{
              width: "100%",
              height: "100%",
              position: "relative",
              paddingBottom: "20px",
              display: "flex",
            }}
          >
            <div
              style={{
                position: "absolute",
                writingMode: "vertical-rl",
                top: "7%",
                transform: "rotate(-180deg)",
                fontSize: "1.111vw",
                letterSpacing: "0.278vw",
                color: "white",
                fontWeight: "bold",
              }}
            >
              WE ARE MAG7
            </div>

            <h1 style={{ paddingLeft: "5.833vw" }}>
              {props.bighead1}
              <br />
              {props.bighead2}{" "}
            </h1>
          </div>
        </div>

        <div
          style={{
            background: "black",
            width: "100%",
            height: "100vh",
            zIndex: "-1",
            opacity: "60%",
          }}
        >
          <img
            style={{
              width: "100%",
              height: "105vh",
              zIndex: "-1",
              objectFit: "cover",
              filter: "grayscale(100%)"
            }}
            src={gifs[Math.floor(Math.random() * gifs.length)]}
          ></img>
        </div>
        <div
          style={{
            background:
              "linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, var(--bg) 100%)",
            width: "100%",
            height: "400px",
            position: "absolute",
            bottom: "-5vh",
          }}
        ></div>
      </div>
    </section>
  );
}

export default Title;
