
function Clients() {


  return (
    <section className="containerbig">
      <div
        style={{
          width: "80vw",
          height: "100%",
          position: "relative",
          paddingBottom: "20px",
          display: "flex",
        }}
      >
        <div
          style={{
            position: "absolute",
            writingMode: "vertical-rl",
            top: "7%",
            transform: "rotate(-180deg)",
            fontSize: "1.111vw",
            letterSpacing: "0.278vw",
            color: "var(--text)",
            fontWeight: "bold",
          }}
        >
          OUR CLIENTS
        </div>
        <h2 style={{ paddingLeft: "5.833vw" }}>
          SAY HELLO <span class="wave"></span> TO A FEW OF OUR FRIENDS
        </h2>
      </div>
      <div style={{ background: "white", padding: "5.556vw" }}>
        <img
          src={require("../images/Clients.png")}
          style={{ width: "100%" }}
        ></img>
      </div>
    </section>
  );
}
export default Clients;
